/* Navbar.css — Replace entire file with this */

/* =========================================
   NAVBAR BASE
========================================= */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 64px;
  padding: 0 40px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
  background: rgba(0, 0, 0, 0.25);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);

  z-index: 1000;
  transition: 0.3s ease;
}

/* Light mode navbar */
body.light-mode .navbar {
  background: rgba(255, 255, 255, 0.6);
  border-bottom-color: rgba(0, 0, 0, 0.15);
}

/* =========================================
   LOGO
========================================= */
.nav-logo {
  font-size: 22px;
  font-weight: 700;
  background: linear-gradient(90deg, #6b5cff, #7df0d9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Light mode */
body.light-mode .nav-logo {
  background: linear-gradient(90deg, #4a17ff, #00c2c7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* =========================================
   DESKTOP LINKS
========================================= */
.nav-links {
  display: flex;
  gap: 28px;
  align-items: center;
}

.nav-links a {
  color: rgba(255, 255, 255, 0.75);
  text-decoration: none;
  font-weight: 500;
  font-size: 15px;
  transition: 0.25s ease;
  position: relative;
}

.nav-links a:hover {
  color: white;
  text-shadow: 0 0 10px rgba(0, 255, 200, 0.4);
}

/* NavLink active styling (react-router) */
.nav-links a.active {
  color: #7df0d9;
  font-weight: 600;
}

/* Light mode links */
body.light-mode .nav-links a {
  color: #242424;
}
body.light-mode .nav-links a.active {
  color: #00bfb4;
}

/* =========================================
   THEME TOGGLE
========================================= */
.theme-toggle {
  width: 52px;
  height: 26px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);

  display: flex;
  align-items: center;
  padding: 3px;
  cursor: pointer;

  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);

  transition: 0.3s ease;
  margin-left: 12px;
}

.toggle-circle {
  width: 20px;
  height: 20px;
  background: var(--accent-grad);
  border-radius: 50%;
  transition: 0.3s ease;
  box-shadow: 0 0 10px rgba(125,240,217,0.4);
}

/* Light mode toggle */
body.light-mode .toggle-circle {
  transform: translateX(24px);
}
body.light-mode .theme-toggle {
  background: rgba(0, 0, 0, 0.2);
  border-color: rgba(125,240,217,0.4);
}

/* =========================================
   MOBILE MENU BUTTON (Hamburger)
   Keep it above everything (z-index).
========================================= */
.menu-btn {
  display: none; /* hidden on desktop */
  flex-direction: column;
  justify-content: center;
  gap: 5px;

  width: 30px;
  height: 24px;
  background: none;
  border: none;
  cursor: pointer;

  z-index: 2100; /* very high so it always receives clicks */
  -webkit-tap-highlight-color: transparent;
}

.menu-btn span {
  width: 100%;
  height: 3px;
  background: white;
  border-radius: 3px;
  transition: 0.3s ease;
}

/* dark mode hamburger color */
body.light-mode .menu-btn span {
  background: #1a1a1a;
}

/* animate open */
.menu-btn.open span:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
.menu-btn.open span:nth-child(2) {
  opacity: 0;
}
.menu-btn.open span:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

/* =========================================
   MOBILE DROPDOWN MENU
   Off-screen + pointer-events: none when hidden
   On-screen + pointer-events: auto when shown
========================================= */
.mobile-menu {
  position: fixed;
  top: 64px;
  right: -110%;
  width: 60%;
  height: calc(100% - 64px);

  backdrop-filter: blur(12px);
  background: rgba(0, 0, 0, 0.6);
  border-left: 1px solid rgba(255, 255, 255, 0.1);

  display: flex;
  flex-direction: column;
  padding: 30px 20px;
  gap: 20px;

  transition: right 0.35s cubic-bezier(.2,.9,.2,1), opacity 0.25s ease;
  z-index: 2000;

  /* IMPORTANT: when hidden don't accept pointer events */
  pointer-events: none;
  opacity: 0;
}

/* show: slide in and become interactive */
.mobile-menu.show {
  right: 0;
  pointer-events: auto;
  opacity: 1;
}

/* Light mode mobile */
body.light-mode .mobile-menu {
  background: rgba(255, 255, 255, 0.92);
}

/* Mobile menu links */
.mobile-menu a {
  color: white;
  font-size: 18px;
  text-decoration: none;
  padding: 6px 0;
  transition: 0.3s ease;
}

.mobile-menu a:hover {
  color: #7df0d9;
}

/* Light mode mobile links */
body.light-mode .mobile-menu a {
  color: #1b1b1b;
}
body.light-mode .mobile-menu a:hover {
  color: #00bfb4;
}

/* =========================================
   RESPONSIVE
========================================= */
@media (max-width: 900px) {
  .nav-links {
    display: none;
  }
  .menu-btn {
    display: flex;
  }
  .navbar {
    padding: 0 20px;
  }
}

@media (max-width: 500px) {
  .nav-logo {
    font-size: 20px;
  }
  .mobile-menu {
    width: 75%;
  }
}